/* eslint-disable no-constant-condition */
import React, { useEffect, useState } from 'react';
import {
  Box, Button, Text, HStack, VStack, Checkbox, FormControl, FormLabel, RadioGroup, Radio
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import DatePicker from 'react-datepicker';
import MiniBox from '../../../layout/MiniBox';
import Icon from '../../../components/Icon';
import style from './TransactionFilter.module.css';
import 'react-datepicker/dist/react-datepicker.css';
import CurrencyInput from '../../../components/CurrencyInput';

type filterStates = {
  transactionType: 'credit' | 'debit' | 'card' | undefined,
  timePeriod: 'thisWeek' | 'thisMonth' | 'lastMonth' | 'custom' | undefined,
  amount: {
    min: string,
    max: string
  },
  startDate: Date,
  endDate: Date
};

type Props = {
  showTransactionTypeFilter?: boolean,
  showTimePeriodFilter?: boolean,
  showAmountFilter?: boolean,
  showCardsFilter?: boolean,
  onApply?: (d: filterStates) => void,
  filters?: filterStates,
}
const TransactionFilter = ({
  showTransactionTypeFilter, showTimePeriodFilter, showAmountFilter, showCardsFilter, filters, onApply
}: Props) => {
  const { t } = useTranslation();
  const [showTransactionTypeData, setShowTransactionTypeData] = useState(false);
  const [showTimePeriodData, setShowTimePeriodData] = useState(false);
  const [showAmount, setShowAmount] = useState(false);
  const [showCardsData, setShowCardsData] = useState(false);

  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());

  const accountType = 'cash';

  const [transactionType, setTransactionType] = useState<filterStates['transactionType']>();

  const [timePeriod, setTimePeriod] = useState<filterStates['timePeriod']>();

  const [cardType, setCardType] = useState({
    physical: false,
    virtual: true,
  });

  const [amount, setAmount] = useState<filterStates['amount']>({
    min: '',
    max: ''
  });

  useEffect(() => {
    if (filters) {
      if (filters.transactionType) {
        setTransactionType(filters.transactionType);
      }
      if (filters.timePeriod) {
        setTimePeriod(filters.timePeriod);
      }
      if (filters.amount) {
        setAmount(filters.amount);
      }
      if (filters.startDate) {
        setStartDate(new Date(filters.startDate));
      }
      if (filters.endDate) {
        setEndDate(new Date(filters.endDate));
      }
    }
  }, [filters]);

  return (
    <div className="page">
      <MiniBox
        heading="Filter"
        showBack
        footer={(
          <Button
            data-testid="submit-btn"
            width="100%"
            variant="primary"
            onClick={() => {
              if (onApply) {
                onApply({
                  transactionType,
                  timePeriod,
                  amount,
                  startDate,
                  endDate
                });
              }
            }}
          >
            {t('filter.ctaText')}
          </Button>
)}
      >
        <Box>

          {/* Filter type */}
          {showTransactionTypeFilter ? (
            <Box data-testid="transaction-type-filter">
              <HStack className={style.filter_header} onClick={() => setShowTransactionTypeData(!showTransactionTypeData)}>
                <Text variant="large">{t('filter.transactionType')}</Text>
                {showTransactionTypeData ? <Icon className="icon-customizable" icon="icon-minus" size={20} /> : <Icon className="icon-customizable" icon="icon-plus" size={20} />}
              </HStack>
              <Box borderBottom="1px solid var(--border-color-1)">
                {showTransactionTypeData ? (
                  <RadioGroup onChange={(v:'card' | 'credit' | 'debit') => v && setTransactionType(v)} value={transactionType}>
                    <VStack alignItems="flex-start" pb="20px">
                      <Radio value="credit" variant="primary">
                        <Text variant="regular" className="text-dark">{t('filter.credits')}</Text>
                      </Radio>
                      <Radio variant="primary" value="debit">
                        <Text variant="regular" className="text-dark">{t('filter.debits')}</Text>
                      </Radio>
                      {accountType === 'cash'
                        ? (
                          <Radio variant="primary" value="card">
                            <Text variant="regular" className="text-dark">{t('filter.cardTransactions')}</Text>
                          </Radio>
                        ) : null }
                    </VStack>
                  </RadioGroup>
                ) : null}
              </Box>
            </Box>
          ) : null}

          {/* Time Period */}
          {showTimePeriodFilter ? (
            <Box data-testid="date-filter">
              <HStack className={style.filter_header} onClick={() => setShowTimePeriodData(!showTimePeriodData)}>
                <Text variant="large">{t('filter.timePeriod')}</Text>
                {showTimePeriodData ? <Icon className="icon-customizable" icon="icon-minus" size={20} /> : <Icon className="icon-customizable" icon="icon-plus" size={20} />}
              </HStack>
              <Box borderBottom="1px solid var(--border-color-1)">
                {showTimePeriodData ? (
                  <RadioGroup>
                    <VStack alignItems="flex-start" pb="20px">
                      <Radio variant="primary" onChange={() => setTimePeriod(timePeriod === 'thisWeek' ? undefined : 'thisWeek')} isChecked={timePeriod === 'thisWeek'}>
                        <Text variant="regular" className="text-dark">{t('filter.thisWeek')}</Text>
                      </Radio>
                      <Radio variant="primary" onChange={() => setTimePeriod(timePeriod === 'thisMonth' ? undefined : 'thisMonth')} isChecked={timePeriod === 'thisMonth'}>
                        <Text variant="regular" className="text-dark">{t('filter.thisMonth')}</Text>
                      </Radio>
                      <Radio variant="primary" onChange={() => setTimePeriod(timePeriod === 'lastMonth' ? undefined : 'lastMonth')} isChecked={timePeriod === 'lastMonth'}>
                        <Text variant="regular" className="text-dark">{t('filter.lastMonth')}</Text>
                      </Radio>
                      <Radio variant="primary" onChange={() => setTimePeriod(timePeriod === 'custom' ? undefined : 'custom')} isChecked={timePeriod === 'custom'}>
                        <Text variant="regular" className="text-dark">{t('filter.custom')}</Text>
                      </Radio>
                      {timePeriod === 'custom'
                        ? (
                          <HStack pt="20px">
                            <FormControl variant="primary">
                              <FormLabel variant="primary">
                                {t('filter.from')}
                              </FormLabel>
                              <DatePicker className={style.input_picker} selected={startDate} onChange={(date:Date) => setStartDate(date)} placeholderText={t('filter.startDate')} />
                            </FormControl>
                            <FormControl variant="primary">
                              <FormLabel variant="primary">
                                {t('filter.to')}
                              </FormLabel>
                              <DatePicker className={style.input_picker} selected={endDate} onChange={(date:Date) => setEndDate(date)} placeholderText={t('filter.endDate')} />
                            </FormControl>
                          </HStack>
                        ) : null }
                    </VStack>
                  </RadioGroup>
                ) : null}
              </Box>
            </Box>
          ) : null}

          {/* Amount */}
          {showAmountFilter ? (
            <Box data-testid="amount-filter">
              <HStack className={style.filter_header} onClick={() => setShowAmount(!showAmount)}>
                <Text variant="large">{t('filter.amount')}</Text>
                {showAmount ? <Icon className="icon-customizable" icon="icon-minus" size={20} /> : <Icon className="icon-customizable" icon="icon-plus" size={20} />}
              </HStack>
              <Box borderBottom="1px solid var(--border-color-1)">
                {showAmount ? (
                  <HStack className={style.amountInputs}>
                    <FormControl variant="primary">
                      <FormLabel variant="primary">
                        {t('filter.from')}
                      </FormLabel>
                      <CurrencyInput
                        value={amount.min}
                        onChange={(value) => setAmount({
                          ...amount,
                          min: value || ''
                        })}
                      />
                    </FormControl>
                    <FormControl variant="primary">
                      <FormLabel variant="primary">
                        {t('filter.to')}
                      </FormLabel>
                      <CurrencyInput
                        value={amount.max}
                        onChange={(value) => setAmount({
                          ...amount,
                          max: value || ''
                        })}
                      />
                    </FormControl>
                  </HStack>
                ) : null}
              </Box>
            </Box>
          ) : null}

          {/* Cards */}
          {showCardsFilter && false ? (
            <Box>
              <HStack className={style.filter_header} onClick={() => setShowCardsData(!showCardsData)}>
                <Text variant="large">{t('filter.cards')}</Text>
                {showCardsData ? <Icon className="icon-customizable" icon="icon-minus" size={20} /> : <Icon className="icon-customizable" icon="icon-plus" size={20} />}
              </HStack>
              <Box borderBottom="1px solid var(--border-color-1)">
                {showCardsData ? (
                  <VStack alignItems="flex-start" pb="20px">
                    <Checkbox onChange={() => setCardType({ ...cardType, physical: !cardType.physical })} isChecked={cardType.physical}>
                      <Text variant="regular">{t('filter.physical')}</Text>
                    </Checkbox>
                    <Checkbox onChange={() => setCardType({ ...cardType, virtual: !cardType.virtual })} isChecked={cardType.virtual}>
                      <Text variant="regular">{t('filter.virtual')}</Text>
                    </Checkbox>
                  </VStack>
                ) : null}
              </Box>
            </Box>
          ) : null}

        </Box>
      </MiniBox>
    </div>
  );
};

export default TransactionFilter;
