import React, { useMemo } from 'react';
import { shallowEqual } from 'react-redux';
import {
  startOfWeek, endOfWeek, startOfMonth, endOfMonth, format
} from 'date-fns';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { updateTransactionFilter } from '../../../store/actionCreators/transaction';
import TransactionFilter from './TransactionFilter';
import { filterData } from '../../../types/transaction';
import { useDrawer } from '../../../hooks/drawer';
import { eventEmitter, Events } from '../../../utils/events';

const Filter = () => {
  const tmpFilters = useAppSelector((store) => store.transaction.filters.tmpStates, shallowEqual);
  const dispatch = useAppAsyncDispatch();
  const { close } = useDrawer();

  const filtersData = useMemo(() => {
    const filterOptions: filterData & {timePeriod?: string} = {};
    const filterObject: any = {
      ...tmpFilters
    };
    if (filterOptions.txnType === 'credit' || filterOptions.txnType === 'debit' || filterOptions.txnType === 'card') {
      filterObject.transactionType = filterOptions.txnType;
    }
    // if (filterOptions.transferType === 'card') {
    //   filterObject.transactionType.cardTransactions = true;
    // }
    if (filterOptions.startDate) { filterObject.startDate = filterOptions.startDate; }
    if (filterOptions.endDate) { filterObject.endDate = filterOptions.endDate; }
    if (filterOptions.timePeriod) { filterObject.timePeriod = filterOptions.timePeriod; }
    if (filterOptions.minAmount) { filterObject.amount.min = filterOptions.minAmount; }
    if (filterOptions.maxAmount) { filterObject.amount.max = filterOptions.maxAmount; }

    return filterObject;
  }, [tmpFilters]);

  return (
    <TransactionFilter
      showAmountFilter
      showCardsFilter
      showTimePeriodFilter
      showTransactionTypeFilter
      filters={filtersData}
      onApply={({
        transactionType, amount, timePeriod, startDate, endDate
      }) => {
        // eslint-disable-next-line @typescript-eslint/no-shadow
        const filters:filterData = {};
        // transactionType
        if (transactionType === 'credit' || transactionType === 'debit' || transactionType === 'card') {
          filters.txnType = transactionType;
        }
        // else if (transactionType === 'card') {
        //   filters.transferType = 'card';
        // }

        // date
        const today = new Date();
        const lastday = new Date(today.getFullYear(), today.getMonth(), 0);
        switch (timePeriod) {
          case 'thisWeek':
            filters.startDate = format(startOfWeek(today), 'yyyy-MM-dd');
            filters.endDate = format(endOfWeek(today), 'yyyy-MM-dd');
            break;
          case 'thisMonth':
            filters.startDate = format(startOfMonth(today), 'yyyy-MM-dd');
            filters.endDate = format(endOfMonth(today), 'yyyy-MM-dd');
            break;
          case 'lastMonth':
            filters.startDate = format(startOfMonth(lastday), 'yyyy-MM-dd');
            filters.endDate = format(endOfMonth(lastday), 'yyyy-MM-dd');
            break;
          case 'custom':
            filters.startDate = format(startDate, 'yyyy-MM-dd');
            filters.endDate = format(endDate, 'yyyy-MM-dd');
            break;
          default:
            break;
        }

        // amount
        if (amount.min !== '') {
          filters.minAmount = amount.min;
        }
        if (amount.max !== '') {
          filters.maxAmount = amount.max;
        }

        dispatch(updateTransactionFilter({
          data: filters,
          tmpStates: {
            transactionType, amount, timePeriod, startDate, endDate
          }
        }));

        setTimeout(() => {
          eventEmitter.dispatch(Events.TRANSACTION_REFRESH, {
            data: filters,
            tmpStates: {
              transactionType, amount, timePeriod, startDate, endDate
            }
          });
        }, 0);

        close();
      }}
    />
  );
};

export default Filter;
