/* eslint-disable no-underscore-dangle */

export enum Events {
    LOGOUT = 'logout',
    CARD_REFRESH = 'card_refresh',
    TRANSACTION_REFRESH = 'transaction_refresh',
    CONTACT_REFRESH = 'contact_refresh',
}

type callbackFn = (data: any) => any;

type Emitter = {
    _events: {
        [key: string]: Array<callbackFn>
    },
    dispatch: (event: Events, data: any) => void
    subscribe: (event: Events, cb: callbackFn) => () => void;
    unsubscribe: (event: Events, index?: number) => void;
}

export const eventEmitter:Emitter = {
  /**
     * events store
     */
  _events: {},
  /**
   * dispatcher
   * @param event
   * @param data
   * @returns
   */
  dispatch(event, data) {
    if (!this._events[event]) return;
    this._events[event].forEach((callback: any) => callback(data));
  },
  /**
   * subscriber
   * @param event
   * @param callback
   */
  subscribe(event, callback) {
    if (!this._events[event]) this._events[event] = [];
    const length = this._events[event].push(callback);
    return () => {
      this.unsubscribe(event, length - 1);
    };
  },
  /**
   * unsubscriber
   * @param event
   * @returns
   */
  unsubscribe(event, index?) {
    if (!this._events[event]) return;
    if (index !== undefined && this._events[event][index]) {
      this._events[event].splice(index, 1, () => {});
    } else {
      delete this._events[event];
    }
  }
};
