import React, { useLayoutEffect, useRef } from 'react';
import { Input } from '@chakra-ui/react';
import Inputmask from 'inputmask';

type Props = {
    value: string;
    placeholder?: string;
    prefix?: string;
    digits?: number;
    onChange: (v: string) => void;
}

const CurrencyInput:React.FC<Props> = ({
  onChange, value, placeholder, prefix, digits
}) => {
  const input = useRef(null);

  useLayoutEffect(() => {
    if (input.current) {
      Inputmask('currency', {
        autoUnmask: true,
        allowMinus: false,
        prefix: prefix ?? '$ ',
        placeholder,
        rightAlign: false,
        digits,
        numericInput: true,
        // positionCaretOnClick: 'ignore'
      }).mask(input.current);
    }
  }, [prefix, digits, placeholder]);

  return (
  // eslint-disable-next-line react/jsx-props-no-spreading
    <Input
      data-testid="currency-input"
      variant="primary"
      placeholder={placeholder || '$ 0.00'}
      ref={input}
      onChange={(e:any) => {
        const v = e.target.value;
        onChange(v);
      }}
      value={value}
    />
  );
};

export default CurrencyInput;
